@keyframes slideInFromTop{
  0% {
    transform: translateY(-100%);
    opacity:0;
  }
  100% {
    transform: translateY(0%);
    opacity:100;
  }
}

@keyframes slideInFromLeft {
    0% {
      transform: translateX(-100%);
      opacity:0;
    }
    100% {
      transform: translateX(0%);
      opacity:100;
    }
  }

  @keyframes fadeIn {
    0% {
      opacity:0;
    }
    100% {
      opacity:100;
    }
  }
